.login-container {
    position: relative; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: hidden;  
    
    &::before {
      content: ''; 
      position: absolute; 
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url('../../../assets//image/test-bg.jpg'); 
      background-size: cover; 
      background-position: center; 
      z-index: -1; 
      filter: grayscale(100%) brightness(80%);
      opacity: 0.1;
    }
  }
  
.login-content-wrapper{
    // border: 1px solid black;    
    padding: 10px;
    padding-top: 41px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);    
    border-radius: 10px;
    height: 300px;
    margin-top: 73px;
    @media (min-width: 760px) and (max-width: 1180px) {
        width: 40%;
    }

  }
  
.login-input-wrapper{
    // border: 1px solid black;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
}
.login-header-title-1 {
    // border: 1px solid pink;
    color: black;
    position: absolute;
    top: 8%;
    width: 33%;
    @media (min-width: 760px) and (max-width: 1180px) {
        top: 27%;
    }
}
.login-header-title {
    background-color: #FFA726;
    text-align: center;
    color: white;
    height: 74px;
    border-radius: 5px;
    padding-top: 17px;
    width: 256px;
    z-index: 9;
    position: absolute;
    margin-bottom: 337px;
}
.button-wrapper{
    margin-top: 20px;
}
.button-login{
    border: 1px solid #02BCD5;
    background-color: #02BCD5;
    color: #fff;
    padding: 20px;
    height: 10px;
    border-radius: 18px;
    margin: 0 auto;
    width: 90%;
    justify-content: center;
    display: flex;
    align-items: center;
    @media (min-width: 760px) and (max-width: 1180px) {
        width: 90% !important;
    }
}
.button-login:hover{
    cursor: pointer;
}
.button-login:active{
    border: 1px solid #049eb3;
    background-color: #049eb3;
    color: #fff;
    padding: 20px;
  
    border-radius: 30px;
    margin: 0 auto;
    margin-bottom: 20px;
    @media (min-width: 760px) and (max-width: 1180px) {
        width: 90%;
    }
}
.text-align-center {
    text-align: center;
}
.text-header {
    // border: 1px solid red;
    padding-bottom: 10px;
}
.form-input{
    width: 100%;
}
.login-footer-title{
    margin-top: 60px;
}