.tab1_container{
    background-color: white;
    max-width: 1920px;
    position: relative;
    min-height: 500px;
    width: 100%;
    margin-top: 100px;
    height: auto;
    padding-bottom: 30px;
    box-shadow: 0 3px 9px gray;
    border-radius: 8px;
    padding: 20px;
    @media (max-width:320px) {
        padding: 10px;
    }
    .over_view {
        position: absolute;
        left: 50%;
        transform: translate(-50%, -80px);
        min-height: 98px;
        height: 98px;
        border-radius: 8px;
        color: white;
        width: 90%;
        background-color: #10b7cb;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @media (max-width:320px) {
            padding: 5px;
            height: auto;
            min-height: 0px;
        }
        .first{
            text-transform: capitalize;
            margin-bottom: 5px;
            font-size: 24px;

            @media (max-width:320px) {
                font-size: 20px;
            }
        }
        .second{
            font-size: 16px;
            text-align: center;
            text-transform: capitalize;
            
            @media (max-width:320px) {
                font-size: 12px;
            }
        }
    }
    
    .camera_section{
        margin-top: 150px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: 1px solid lightgray;
        padding-bottom: 20px;
        @media (max-width:840px) {
            margin-top: 90px;
        }
        @media (max-width:320px) {
            margin-top: 10px;
        }
        .camera_container{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            max-width: 700px;
            border: 1px dotted black;
            margin-top: 20px;
            padding-bottom: 20px;
            height: 500px;
            position: relative;
            @media (max-width:540px) {
                height: 350px;
            }
            @media (max-width:320px) {
                height: 300px;
            }
            .label_1{
                font-size: 40px;
                font-weight: 700;
                text-align: center;
                line-height: 40px;
                margin-top: 10px;
                @media (max-width:440px) {
                    margin-top: 10px;
                    font-size: 35px;
                }
                @media (max-width:320px) {
                    margin-top: 10px;
                    font-size: 25px;
                }
                
            }
            .label_2{
                text-align: center;
                line-height: 40px;
                font-size: 20px;
                @media (max-width:440px) {
                    // margin-top: 10px;
                }
                @media (max-width:320px) {
                    font-size: 14px;
                }
            }
            .camera_icon{
                margin-top: 40px;
                width: 250px;
                height: 250px;
                @media (max-width:320px) {
                   margin-top: 10px;
                }
                .cam_icon{
                    color: lightgray;
                    font-size: 250px;
                    cursor: pointer;
                    @media (max-width:540px) {
                        height: 150px;
                    }
                }
            }
        }
    }

    .btn_section{
        display: flex;
        justify-content: center;
        margin-top: 20px;
        position: relative;
        min-height: 50px;
        .save_btn{
            padding: 10px;
            background-color: #10b7cb;
            color: white;
            font-size: 20px;
            width: 200px;
            position: absolute;
            right: 0;

            @media (max-width:440px) {
                width: 100%;
            }
        }   
    }

}