.interview_main_container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;

    .tab_container {
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-top: 64px;

        .tabs {
            flex: 1;
            text-transform: none;
            color: #000000;

            &.Mui-selected {
                color: green;
            }
        }
        
        .MuiTabs-indicator {
            background-color: green;
        }
    }

    @media  screen and (max-width: 440px) {
        .tab_container {
            .tabs {
                font-size: 12px;
                min-width: unset;
            }
        }
    }

    @media (max-width:320px) {
        padding: 5px;
    }


}