@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
#RegistrationPage{
    width: 100%;
    // background: rgba($color: #e1f8ff, $alpha: 1.0);
    font-family: "Poppins", sans-serif;

    .registrationContainer{
        max-width: 980px;
        margin: 0 auto;
    }

    .text-title-lg{
        font-size: 24px;
        color: #000000
    }

    .text-p-lg{
        margin-top: 10px;
        font-size: 15px;
        color: #2623D2
    }

    .mobileStepper{
        margin-top: 20px;
        justify-content: center;
        // background: rgba($color: #e1f8ff, $alpha: 1.0);
    }

    .termsConditionContainer, .qualificationContainer, .personalDetailContainer, .contactDetailContainer, .otherDetailContainer{
        padding: 20px;
    }
    
    .termsConditionContainer{
        overflow: hidden;
    }

    .tncContentContainer{
        margin-top: 20px;

        .tncContent{
            margin-top: 10px;
            height: 100vh;
            overflow-y: scroll;
            width: 100%;
            border-bottom: 1px solid #e1e1e1;
            padding-bottom: 20px;
        }

        p{
            font-size: 13px;
            margin-top: 15px;
        }
        h2{
            color: #999999;
            font-size: 13px;
            margin-bottom: 20px;
        }
        h3{
            color: #2623D2;
            font-size: 14px;
            margin-bottom: 10px;
        }
    }

    .tncBoardDetail{
        background: rgba(244, 244, 244, 0.8);
        border: 1px solid #e1e1e1;
        border-radius: 5px;
        padding: 15px 16px;
    }

    .tncFooterContainer{
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
    }

    .tncAgreeBox-right{
        flex: 1;
        font-size: 14px;
    }

    .inputContainer{
        margin-top: 20px;
        label{
            font-size: 14px;
        }
    }


    .formContentContainer{
        margin-top: 20px;

        p.form-label{
            font-size: 16px;
        }

        p.form-label-notavailable{
            font-size: 14px;
            color: #797979;

        }
    }

    .inputField{
        width: 100%;
    }

    .formBoardDetail{
        background: rgba(244, 244, 244, 0.8);
        border: 1px solid #e1e1e1;
        border-radius: 5px;
        padding: 15px 16px;
        
        h2{
            font-size: 13px;
            color: #999999;
            margin-bottom: 15px;
        }
        h3{
            color: #000000;
            font-size: 14px;
            margin-bottom: 5px;
        }
        p.btm-text{
            font-size: 10px;
            font-style: italic;
            margin-bottom: 15px;
        }
    }

    .formContent{
        display: flex;
        flex-direction: row;
        font-size: 13px;
        justify-content: center;
        align-items: center;
    }

    .formContent-left{
        flex: 1;
        span{
            margin-top: -4px
        }
    }

    .formContent-right{
        flex: 1;
        span{
            margin-top: -4px
        }
    }

    .interviewDate{
        margin-top: 10px;
        height: 40px;
        font-size: 14px;
    }

    .inputRow{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        .inputColLeft{
            width: 45%;
        }
        .inputColRight{
            width: 45%;
        }
    }

    .selectMenutItem{
        font-size: 14px;
    }

    .photoPlaceholder{
        width: 100%;
        height: 150px;
        justify-content: center;
        align-items: center;
        text-align: center;
        border: 1px dashed #989898;
        border-radius: 8px;
        display: flex;
    }

    .photoButtonContainer{
        align-self: center;
    }
}