#interview_dashboard{
    width: 100%;
    .parent{
       max-width: 1920px;
       width: 100%;
       margin:0 auto;
       padding: 16px;
       
        .cardParent{
            flex-direction: row;
            flex-wrap: wrap;
        }
    
        .attendance_interview{
            flex-direction: row;
            padding-top: 20px;
            padding-bottom: 20px;
            padding-left: 10px;
            padding-right: 10px;
            gap: 10px;
            margin-bottom: 50px;
        }

        @media only screen and (max-width: 640px) { 
            .attendance_interview{
                flex-direction: column;
            }
        }

        @media only screen and (max-width: 547px) {
            .attendance_interview{
                padding-left: 0px;
                padding-right: 0px;
            }
        }
        
    }


