#login{
    h5.title{
        font-size: 24px;
        line-height: 30px;
        font-weight: 700;
    }
    p.desc{
        color: #969696;
        font-size: 16px;
        line-height: 18px;
        margin-top: 10px;
    }
    #form{
        padding: 20px 0;
        .form{
            margin-top: 15px;
            .keep{
                font-size: 16px;
                line-height: 18px;
                color:#232323;
                display: flex;
            }
            .nacct{
                font-size: 18px;
                line-height: 20px;
                color: #6C6C6C;
                text-align: center;
                margin: 2px 0;
            }
            .cone{
                font-size: 18px;
                line-height: 20px;
                color: #2623D2;
                font-weight: 700;
                text-decoration: underline;
                text-align: center;
                margin: 2px 0 2px 5px;
                cursor: pointer;
            }
        }
    }
}