.inteDet{
    width:"100%";
    .con{
        min-width: 275px;
        background:#FFF;
        position: relative;
        overflow: visible;
        padding: 0 20px;
        @media  screen and (max-width: 440px) {
            width: 100%;
            padding: 0 10px;
        }
        .conHeader{
            background: rgb(38,198,218);
            background: linear-gradient(90deg, rgba(38,198,218,1) 0%, rgba(1,173,192,1) 28%, rgba(1,173,194,1) 100%);
            position: absolute;
            width: 95%;
            margin: -30px auto 0;
            left: 0;
            right: 0;
            border-radius: 10px;
            @media  screen and (max-width: 440px) {
                h4{
                    font-size: 25px;
                }
            }
        }
        .conContent{
            padding-top: 120px;
            @media  screen and (max-width: 440px) {
                padding-top: 90px;
                padding-left: 0;
                padding-right: 0;
            }
            .info_img{
                flex-direction: row;
                align-items: center;
                justify-content: space-evenly;
                flex-wrap: wrap;
                @media  screen and (max-width: 480px) {
                    justify-content: flex-start;
                }
            }
            .detCon{
                width: 90%;
                @media  screen and (max-width: 980px) {
                    width: 85%;
                }
                @media  screen and (max-width: 480px) {
                    order: 2;
                    width: 100%;
                    margin-top: 10px;
                }
                .detlbl{
                    width: 200px;
                    @media  screen and (max-width: 980px) {
                        width: 190px;
                    }
                    @media  screen and (max-width: 640px) {
                        font-size: 16px;
                    }
                  
                }
                .detlblVal{
                    @media  screen and (max-width: 640px) {
                        font-size: 16px;
                    }
                }
                
            }
            .empPhoto{
                max-width: 150px;
                width: 10%;
                @media  screen and (max-width: 980px) {
                    width: 15%;
                }
                @media  screen and (max-width: 480px) {
                    margin-bottom: 10px;
                    width: 20%;
                }
            }
        }
        .scanEmpQR{
            border-radius: 50px;
            margin-bottom: 20px;
            margin-left: 16px;
            @media  screen and (max-width: 440px) {
                font-size: 12px;
                margin-left: 0;
                width: 100%;
            }
        }
    }
   
}
.scanQrCon{
    padding-top: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    border: 0px solid #000;
    height:100vh;
    background-color: #FFF;
}