#onboarding{
    background-color: #FFFF;
    .hero{
        padding: 350px 20px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        @media  screen and (max-width: 1440px) {
            padding: 300px 20px;
        }
        @media  screen and (max-width: 1280px) {
            padding: 250px 20px;
        }
        @media  screen and (max-width: 1100px) {
            padding: 180px 20px;
        }
    }
    .head_desc{
        padding: 30px 0;
        background-color: #FFFF;
        h3{
            font-size: 28px;
            line-height: 34px;
            font-weight: 600;
        }
        h6{
            font-size: 14px;
            line-height: 18px;
            margin-top: 10px;
            color: rgba(0,0,0,.7);
        }
        h3,h6{
            text-align: center;
        }
    }
    .slick-slider{
        // .slick-arrow{
        //     display: none !important;
        // }
        .slick-dots{
            bottom: -2;
            li{
                button::before{
                    font-size: 16px;
                }
            }
            li.slick-active{
                button::before{
                    color: #2623D2;
                }
            }
        }
    }
}