#attendance_screen{
    width: 100%;
    .as_main{
        max-width: 1920px;
        margin:0 auto;
        padding:20px;

        .as_header{
            flex-direction: row;

            .attendance_container{
                display: flex;
                flex: 1;
                align-items: center;
            }
        }

       

        .main_card{
            padding: 16px;
            position: relative;
            margin-top: 5%;

            .main_card_header{
                padding:10px;
                background-color: #07f332;
                border-radius: 4px;
                position: absolute;
                width: 90%;
                top:-3%;
                left: 50%;
                transform: translate(-50%);
            }

            @media only screen and (max-width: 550px) { 
                .main_card_header{
                    padding:7px;
                }
            }

            .card_white_bg{
                background-color: #fff;
                box-shadow: 0 3px 9px gray;
                padding:20px;
                border-radius: 8px;
                
                .card_img_container{
                    margin-top: 5%;
                }
    
                .card_btn_take{
                    background-color: #07f332;
                    min-width: 150px;
                    padding: 7px;
                    justify-content: center;
                    align-items: center;
                    border-radius: 5px;
                }

                .card_btn_cancel{
                    background-color: #f30707;
                    min-width: 100px;
                    padding: 7px;
                    justify-content: center;
                    align-items: center;
                    border-radius: 5px;
                }

                .card_btn_take:hover {
                    background-color: #03d129;
                }

                .card_btn_confirm{
                    background-color: #07d7f3c7;
                    min-width: 100px;
                    padding: 10px;
                    justify-content: center;
                    align-items: center;
                    border-radius: 5px;
                    align-self: flex-end;
                    cursor: pointer;
                }

                .card_btn_confirm:hover {
                    background-color: #05b0c7c7;
                }

                @media only screen and (max-width: 640px) { 
                    .inner-page-container{
                        .title_page{
                            font-size: 10;
                        }
                        .value_page{
                            font-size: 10;
                        }
                    }
                }
                
            }

            @media only screen and (max-width: 640px) { 
                .card_white_bg{
                    padding:10px;
                }
            }

            
        }

        @media only screen and (max-width: 640px) { 
            .main_card{
                padding:14px;
            }
        }
    }

    @media only screen and (max-width: 640px) { 
        .as_main{
            padding:14px;
        }
    }

    @media only screen and (max-width: 440px) { 
        .as_main{
            padding:8px;
        }
    }

}

