#econtact_tab {
    width: 100%;
    display: flex;
    
    .main_container {
        margin-top: 40px;

        .header_container {
            background-color: green;
            padding: 20px;
            border-radius: 8px;
            margin-left: 48px;
            margin-right: 48px;
            position: relative;

            .title_text {
                color: #FFFFFF;
                font-weight: 700;
                font-size: 24px;
                margin-bottom: 20px;
            }

            .title_reg_no {
                color: #FFFFFF;
                font-size: 16px;
            }
        }

        .contract_container {
            background-color: #FFFFFF;
            padding: 16px;
            padding-top: 80px;
            margin-top: -50px;
            border-radius: 8px;
            box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
            display: flex;
            flex-direction: column;

            .loader {
                display: flex;
                justify-content: center;
            }

            .contract_content_container {
                padding: 32px;
            }

            .signature_container {
                flex-direction: row;
                display: flex;
                align-items: center;
                width: 100%;
                padding: 32px;

                .company_address {
                    width: 45%;
                }

                .witness_signature {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 25%;

                    .title_text {
                        color: #000000;
                        font-size: 16px;
                        margin-bottom: 16px;
                        text-align: center;
                    }

                    .sigCanvas {
                        border: 1px solid;
                        border-color: #000000;
                        width: 90%;
                        height: 200px;
                    }
                }

                .signature {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 30%;

                    .title_text {
                        color: #000000;
                        font-size: 16px;
                        margin-bottom: 16px;
                        text-align: center;
                    }

                    .sigCanvas {
                        border: 1px solid;
                        border-color: #000000;
                        width: 90%;
                        height: 200px;
                    }
                }
            }

            .clear_button {
                width: 100px;
                padding: 8px;
                color: #FFFFFF;
                background-color: #666666;
                border: none;
                margin-top: 16px;
                margin-bottom: 16px;
                align-self: flex-end;
                margin-right: 24px;
                border-radius: 4px;
            }

            .footer_container {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                .page_text {
                    color: #000000;
                }

                .next_button {
                    width: 200px;
                    padding: 10px;
                    color: #FFFFFF;
                    background-color: #10b7cb;
                }
            }
        }
    }

    @media  screen and (max-width: 767px) {
        .contract_container {
            .signature_container {
                .witness_signature {
                    .title_text {
                        margin-bottom: 37px !important;
                    }
                }
            }
        }
    }

    @media  screen and (max-width: 640px) {
        .contract_container {
            .signature_container {
                flex-direction: column !important;
                .company_address {
                    width: 100% !important;
                }

                .witness_signature {
                    width: 70% !important;
                    margin-bottom: 16px;

                    .title_text {
                        margin-top: 16px;
                        margin-bottom: 16px !important;
                    }
                }

                .signature {
                    width: 70% !important;

                    .title_text {
                        margin-top: 16px;
                        margin-bottom: 16px !important;
                    }
                }
            }
        }
    }

    @media  screen and (max-width: 440px) {
        .contract_container {
            .signature_container {
                .witness_signature {
                    width: 90% !important;
                }

                .signature {
                    width: 90% !important;
                }
            }
        }
    }
}