#property_tab {
    width: 100%;
    display: flex;
    
    .main_container {
        margin-top: 40px;

        .header_container {
            background-color: green;
            padding: 20px;
            border-radius: 8px;
            margin-left: 48px;
            margin-right: 48px;
            position: relative;

            .title_text {
                color: #FFF;
                font-weight: 700;
                font-size: 24px;
                margin-bottom: 20px;
            }

            .title_reg_no {
                color: #FFF;
                font-size: 16px;
            }
        }

        .page_container {
            background-color: #FFF;
            padding: 48px;
            padding-top: 80px;
            margin-top: -50px;
            border-radius: 8px;
            box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
            display: flex;
            flex-direction: column;

            .signature_main_container {
                display: flex;
                flex-direction: row;

                .signature_container {
                    display: flex;
                    align-self: flex-start;
                    flex-direction: column;
                    width: 40%;

                    .signature_text {
                        color: #000;
                        font-size: 16px;
                        margin-bottom: 16px;
                    }

                    .sigCanvas {
                        border: 1px solid #000;
                        width: 80%;
                        height: 200px;
                    }
                }

                .date_container {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 60%;

                    .date_text {
                        color: #000;
                        font-size: 16px;
                        margin-bottom: 16px;
                    }

                    .date_value_text {
                        color: #000;
                        font-size: 16px;
                    }
                }
            }

            .signDirectorCanvas {
                border: 1px solid #000;
                width: 20%;
                height: 200px;
            }

            .clear_director_sign_button {
                width: 100px;
                padding: 4px;
                color: #FFFFFF;
                background-color: #666666;
                border: none;
                margin-top: 16px;
                margin-bottom: 16px;
                margin-right: 24px;
                border-radius: 4px;
            }

            .clear_button {
                width: 100px;
                padding: 10px;
                color: #FFFFFF;
                background-color: #666666;
                border: none;
                margin-top: 16px;
                margin-bottom: 16px;
                margin-right: 24px;
                border-radius: 4px;
            }

            .next_button {
                width: 200px;
                padding: 10px;
                color: #FFFFFF;
                background-color: #10b7cb;
                align-self: flex-end;
            }
        }
    }

    @media  screen and (max-width: 640px) {
        .page_container {
            .signature_main_container {
                .signature_container {
                    .sigCanvas {
                        width: 100% !important;
                    }
                }
            }
        }
    }

    @media  screen and (max-width: 440px) {
        .page_container {
            .signature_main_container {
                flex-direction: column !important;

                .signature_container {
                    width: 100% !important;
                    margin-bottom: 16px;
                    .sigCanvas {
                        width: 70% !important;
                    }
                }

                .date_container {
                    width: 100% !important;
                }
            }
        }
    }

    @media  screen and (max-width: 320px) {
        .page_container {
            .signature_main_container {
                .signature_container {
                    .sigCanvas {
                        width: 90% !important;
                    }
                }
            }
        }
    }
}