.tab2_container{
    background-color: white;
    max-width: 1920px;
    position: relative;
    min-height: 500px;
    width: 100%;
    margin-top: 100px;
    height: auto;
    padding-bottom: 30px;
    box-shadow: 0 3px 9px gray;
    border-radius: 8px;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
    @media (max-width:540px) {
        padding-left: 15px;
        padding-right: 15px;
    }
    .tab2_overview{
        position: absolute;
        left: 50%;
        transform: translate(-50%, -80px);
        min-height: 98px;
        height: 98px;
        border-radius: 8px;
        color: white;
        width: 95%;
        background-color: #008f0c;
        display: flex;
        justify-content: center;
        padding: 20px;

        flex-direction: column;
        @media (max-width:430px) {
            padding: 15px;
            height: auto;
            min-height: 0px;
        }
        .first{
            text-transform: capitalize;
             margin-bottom: 8px;
            font-size: 24px;

            // @media (max-width:320px) {
            //     font-size: 20px;
            // }
        }
        .second{
            font-size: 16px;
            text-transform: capitalize;
            
            // @media (max-width:320px) {
            //     font-size: 12px;
            // }
        }
    }
    .details_container{
        margin-top: 60px;
        padding-bottom: 40px;
        border-bottom: 1px solid lightgray;
        @media (max-width:430px) {
            margin-top: 60px;
        }
    }
    .emergency_contact{
        margin-top: 20px;
        padding-bottom: 60px;
        border-bottom: 1px solid lightgray;
        .first{
            font-size: 24px;
            font-weight: 700;
        }
    }

    .consent{
     margin-top: 20px;
     padding-bottom: 20px;
     border-bottom: 1px solid lightgray;
        .first{
            font-size: 18px;
            font-weight: 700;
        }
        .second{
            font-size: 16px;
            text-align: justify;
        }

        .signature_section{
            margin-top: 20px;
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            margin-bottom: 20px;
            .left{
                width: 100%;
                max-width: 400px;
                @media (max-width:1110px) {
                    max-width: 50%;
                }
                @media (max-width:767px) {
                    max-width: 60%;
                }
                @media (max-width:680px) {
                    max-width: 100%;
                }
                .signature_label{
                    margin-bottom: 10px;
                    font-size: 18px;
                }
                .signature_container{
                    width: 100%;
                    max-width: 300px;
                    height: 200px;
                    border: 1px solid black;
                }
                .clear{
                    width: 150px;
                    margin-top: 10px;
                    padding: 10px;
                    background-color: gray;
                    color: white;
                }
            }
            .right{
                width: 100%;
                max-width: 500px;
                @media (max-width:1110px) {
                    max-width: 30%;
                }
                @media (max-width:680px) {
                    max-width: 100%;
                    
                }
                .first{
                    font-weight: 400;
                    font-size:  18px;
                    @media (max-width:680px) {
                        margin-top: 10px;   
                    }
                }
                .second{
                    margin-top: 10px;
                    font-size:  18px;
                    @media (max-width:680px) {
                        margin-top: 5px;   
                    }
                }
                
            }
        }
    }

    .btn_applicant{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        padding-top: 10px;
        .btn_1{
            padding: 10px;
            width: 200px;
            background-color: red;
            color: white;
        }
        .btn_2{
            padding: 10px;
            width: 200px;
            margin-left: 20px;
            background-color: #10b7cb;
            color: white;
        }

    }

    .user_details_container{
        display: flex;
        margin-top: 10px;
        flex-direction: row;
        .label{
            max-width: 250px;
            width: 100%;
            font-size: 20px;

            @media (max-width:540px) {
                font-size: 16px;
            }
            
        }
        .value{
            width: 100%;
            font-size: 20px;
            max-width: 250px;
            @media (max-width:540px) {
                font-size: 16px;
            }
        }
    }
}