#dashboard{
    padding-bottom: 70px;
    #header{
        background-color: #2623D2;
        padding: 20px 0 35px;
        .gm{
            color: #FFF;
            font-size: 14px;
            line-height: 18px;
            font-weight: 700;
        }
        .profname{
            color: #FFF;
            font-size: 24px;
            line-height: 30px;
            font-weight: 700;
        }
        .prof{
            width: 50px;
            height: 50px;
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
    #availWrk{
        padding: 30px 0;
        p.head{
            color: #000;
            font-size: 24px;
            line-height: 30px;
            font-weight: 700;
        }
        p.vas{
            color: #2623D2;
            font-size: 12px;
            line-height: 30px;
            text-decoration: underline;
            cursor: pointer;
        }
        .red_dot{
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: red;
        }
        p.ras{
            color: #FFF;
            font-size: 24px;
            line-height: 30px;
            font-weight: 700;
            @media  screen and (max-width: 440px) {
                font-size: 20px;
                line-height: 23px;
            }
        }
        p.slot{
            color: #FFF;
            font-size: 18px;
            line-height: 23px;
            margin-top: 10px;
        }
    }
    #acctSum{
        padding: 30px 0;
        background-color: #f4f4f4;
        p.head{
            color: #000;
            font-size: 24px;
            line-height: 30px;
            font-weight: 700;
        }
        p.value{
            font-size: 24px;
            line-height: 30px;
            font-weight: 700;
        }
        p.desc{
            font-size: 18px;
            line-height: 23px;
            margin-top: 10px;
        }
        p.vas{
            color: #2623D2;
            font-size: 12px;
            line-height: 30px;
            text-decoration: underline;
            cursor: pointer;
        }
    }
    #botNav{
        padding: 10px 0 15px;
        background-color: #2623D2;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        position: fixed;
        bottom: 0;
        width: 100%;
        .navs{
            button{
                color: #FFF;
                font-size: 20px;
                &:hover{
                    color: #2623D2;
                    background-color: #FFF;
                    
                }
            }

        }
    }
}