#edit_modal_container{
    box-shadow: 0 2px 9px gray;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    max-width: 1920px;
    justify-content: space-between;
    padding: 20px;
    margin-bottom: 20px;
    position: relative;
    border-radius: 10px;
    margin-top: 100px;
    .tab2_overview{
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50%, -60px);
        min-height: 98px;
        height: 98px;
        border-radius: 8px;
        color: white;
        width: 95%;
        background-color: #10b7cb;
        display: flex;
        justify-content: center;
        padding: 20px;

        flex-direction: column;
        @media (max-width:430px) {
            padding: 15px;
            height: auto;
            min-height: 0px;
            transform: translate(-50%, -80px);
        }
        .first{
            text-transform: capitalize;
             margin-bottom: 8px;
             font-size: 24px;

            // @media (max-width:320px) {
            //     font-size: 20px;
            // }
        }
        .second{
            font-size: 16px;
            text-transform: capitalize;
            
            // @media (max-width:320px) {
            //     font-size: 12px;
            // }
        }
    }


    .interview_date{
        border-top:1px solid  lightgray;
        width: 100%;
        padding-top: 15px;
        margin-bottom: 20px;

        @media (max-width:540px) {
            margin-bottom: 10px;
        }
    }

    .header{
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        margin-bottom: 20px;
        margin-top: 40px;
    }

    .btn_holder{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        padding-top: 10px;
        margin-top: 20px;
        width: 100%;
        .btn_1{
            padding: 10px;
            max-width: 250px;
            width: 100%;
            background-color: red;
            color: white;
        }
        .btn_2{
            padding: 10px;
            max-width: 200px;
            width: 100%;
            margin-left: 20px;
            background-color: #10b7cb;
            color: white;
        }
    }

    .postal_code_container{
        width: 48%;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .btn_1_postal{
            width: 25%;
            background-color: #10b7cb;
            color: white;
            text-transform: uppercase;
            border-radius: 20px;
            padding: 5px;
        }
    }
}