.ai_main{
    padding:16px;
    position: relative;

    .ai_sub_name{
        position: absolute;
        top:-50px;
        z-index: 1;
        width: 90%;
        left:50%;
        transform: translate(-50%);

        .ai_sub_sub_name{
            padding:10px;
            background-color: #18bdc4;
            border-radius: 4px;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            margin-top: 30px;
            .ai_btn_refresh{
                background-color: #2febf2;
                justify-content: center;
                align-items: center;
                min-width: 40px;
                height: 35px;
            }
        }

    }
    
}

@media only screen and (max-width: 547px) {
    .ai_main{
        padding: 0px;
    }
}